$type-large: '(min-width: 1366px)';

@mixin display-text {
  font-size: rem(33);
  font-weight: 600;
  line-height: 1.2121;
  @media #{$type-large} {
    font-size: rem(48);
    line-height: 1.25;
  }
}
@mixin h1 {
  font-size: rem(27);
  font-weight: bold;
  line-height: 1.1851;

  @media #{$type-large} {
    font-size: rem(33);
    line-height: 1.2121;
  }
}

@mixin h2 {
  font-size: rem(24);
  font-weight: bold;
  line-height: 1.333;

  @media #{$type-large} {
    font-size: rem(27);
    line-height: 1.1851;
  }
}

@mixin h3 {
  font-size: rem(21);
  font-weight: bold;
  line-height: 1.333;
}

@mixin h4 {
  font-size: rem(19);
  font-weight: bold;
  line-height: 1.2631;
}

@mixin h5 {
  font-size: rem(16);
  font-weight: bold;
  line-height: 1.25;
}

@mixin paragraph {
  font-size: rem(16);
  font-weight: normal;
  line-height: 1.5;
}

@mixin paragraph-large {
  font-size: rem(18);
  font-weight: normal;
  line-height: 1.3333;
  @media #{$type-large} {
    font-size: rem(20);
    line-height: 1.4;
  }
}

@mixin paragraph-small {
  font-size: rem(14);
  font-weight: normal;
  line-height: 1.4285;
}

@mixin interactive-text {
  font-size: rem(16);
  font-weight: 600;
  line-height: 1.5;
}

@mixin interactive-text-small {
  font-size: rem(14);
  font-weight: 600;
  line-height: 1.4285;
}

@mixin button-text {
  font-size: rem(18);
  font-weight: bold;
  line-height: 1.1111;
}

@mixin button-text-small {
  font-size: rem(14);
  font-weight: bold;
  line-height: 1.4285;
}

@mixin link-text {
  color: var(--link-color);
  font-size: rem(16);
  font-weight: 600;
  line-height: 1.5;
  text-decoration-line: underline;

  &:hover {
    text-decoration: none;
  }
}

@mixin link-text-small {
  color: var(--link-color);
  font-size: rem(14);
  font-weight: 600;
  line-height: 1.4285;
  text-decoration-line: underline;

  &:hover {
    text-decoration: none;
  }
}

@mixin caption-text {
  font-size: rem(14);
  font-weight: 600;
  line-height: 1.4285;
}

@mixin tags-text {
  font-size: rem(12);
  font-weight: bold;
  letter-spacing: 0.5px;
  line-height: 1.333;
}
