@mixin input-elements {
  @include interactive-text;

  -webkit-appearance: none;
  appearance: none;
  background: var(--white, #ffffff);
  border: 2px solid var(--input-focus, #284b9b);
  box-sizing: border-box;
  display: grid;
  margin: 0 12px;
  place-content: center;
  transform: translateY(-0.075rem);

  &.error {
    border: 2px solid var(--input-error, #e10900);
  }

  &:disabled {
    border: 2px solid var(--input-disabled, #a5aaaa);
  }

  &:focus-visible {
    outline: 2px solid var(--input-button-focus, #0082aa);
    outline-offset: 2px;
  }

  &:checked::before {
    background-color: var(--input-focus, #284b9b);
    border-radius: 50%;
    box-shadow: inset 1rem 1em var(--input-focus, #284b9b);
    content: '';
  }

  &:disabled:checked::before {
    background-color: var(--input-disabled, #a5aaaa);
    box-shadow: inset 1rem 1em var(--input-disabled, #a5aaaa);
  }

  &:hover:enabled {
    box-shadow: 0 0 0 8px var(--input-button-hover, rgba(50, 50, 100, 0.08));
  }
}
