@mixin text-inputs {
  .label-element {
    display: inline-block;
    font-size: rem(14);
    line-height: line-height(20, 14);
    width: var(--input-label-width);
  }

  .text-element {
    @include input-elements;

    border: 1px solid var(--input-default);
    border-radius: var(--border-radius, 8px);
    color: var(--input-default);
    display: block;
    font-weight: 600;
    height: rem(48);
    margin-left: 0;
    max-width: var(--max-width-input);
    min-width: var(--min-width-input);
    padding: 12px 12px 12px 16px;
    width: 100%;

    // disabled & readonly attributes
    &:read-only {
      background: var(--input-disabled-background);
      color: var(--input-text-read-only);
    }

    // disabled only
    &:disabled {
      border-width: 1px;
      color: var(--input-text-disabled);
    }

    &:focus-visible {
      border: 2px solid var(--input-focus);
      outline: none;
      padding: 11px 11px 11px 15px;
    }

    &.error {
      border: 1px solid var(--input-error);

      &:focus-visible {
        border: 2px solid var(--input-error);

        &:hover:enabled {
          border: 2px solid var(--input-error);
        }
      }

      &:hover:enabled {
        border: 2px solid var(--input-error);
        box-shadow: none;
        color: var(--text-color);
        padding: 11px 11px 11px 15px;
      }
    }

    &:valid {
      color: var(--text-color);
    }

    &:hover:enabled {
      border: 2px solid var(--input-hover);
      box-shadow: none;
      color: var(--text-color);
      padding: 11px 11px 11px 15px;

      &:focus-visible {
        border: 2px solid var(--input-focus);
        outline: none;
        padding: 11px 11px 11px 15px;
      }
    }
  }

  .error-element {
    color: var(--input-error);
    font-size: rem(14);
    font-weight: 600;
    line-height: line-height(20, 14);
  }

  .required:not(.disabled):not(.readonly)::before {
    color: var(--input-error);
    content: '*';
    margin-left: 4px;
  }
}
